.home {
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  max-width: 1380px;
}
.home_row {
  display: flex;
  z-index: 1;
  margin-left: 20px;
  margin-right: 20px;
}
.home_image {
  width: 100%;
  z-index: 11;
  margin-bottom: -150px;
  mask-image: linear-gradient(to bottom, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
}

@media only screen and (max-width: 600px) {
  .home {
    max-width: 600px;
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    padding-right: 5px;
  }

  .home_container {
    width: 100%;
  }

  .home_image {
    margin-bottom: -60px;
  }
}
